import React, {Component} from 'react'
import {List, DatePicker, WhiteSpace} from "antd-mobile";
const Item = List.Item;
function formatDate(date) {
    /* eslint no-confusing-arrow: 0 */
    const pad = n => n < 10 ? `0${n}` : n;
    const dateStr = `${date.getFullYear()}-${pad(date.getMonth() + 1)}`;
    return `${dateStr}`;
}
class Salary extends Component {
    constructor(props) {
        super(props);
        this.state = {date: null}
    }
    getSalary = async ()=>{
    }
    onChange=(date)=>{
        this.setState({date},this.getSalary)
    }
    render() {
        return (<div>
            <List className="date-picker-list" renderHeader={() => ''}  style={{backgroundColor: 'white'}}>
                <DatePicker
                    mode="month"
                    value={this.state.date}
                    format={val => `${formatDate(val)}`}
                    onChange={this.onChange}
                >
                    <List.Item arrow="horizontal"></List.Item>
                </DatePicker>
            </List>
            <WhiteSpace />
            <List renderHeader={() => ''} className="my-list">
                <Item extra={1000}>	:FPA1006</Item>
                <Item extra={1001}>	:FPA15</Item>
                <Item extra={1002}>	FPA17</Item>
            </List>
            <List renderHeader={() => ''} className="my-list">
                <Item extra={1003}>: FPA19</Item>
                <Item extra={1004}>: FPA1007</Item>
                <Item extra={1005}>: FPA1008</Item>
                <Item extra={1006}>: FPA1009</Item>
                <Item extra={1007}>: FPA1010</Item>
                <Item extra={1008}>: FPA1011</Item>
                <Item extra={1009}>: FPA1012</Item>
                <Item extra={1010}>: FPA1013</Item>
                <Item extra={1011}>: FPA1014</Item>
                <Item extra={1012}>FPA1015</Item>
                <Item extra={1013}>FPA1016</Item>
                <Item extra={1014}>FPA1017</Item>
                <Item extra={1015}>FPA23</Item>
                <Item extra={1016}>: FPA22</Item>
                <Item extra={1017}>FPA1018</Item>
                <Item extra={1018}>FPA1019</Item>
                <Item extra={1019}>FPA1020</Item>
                <Item extra={1020}>FPA1024</Item>
                <Item extra={1021}>FPA1021</Item>
                <Item extra={1022}>FPA1022</Item>
                <Item extra={1023}>FPA18</Item>
                <Item extra={1024}>FPA1023</Item>
            </List>
        </div>);
    }
}

export default Salary;


